
import { defineComponent,reactive,toRefs,onUpdated,ref,onMounted,watch } from 'vue'
import {getJson} from "@/api/apiConfig";
import { playtime,audioPlay } from "@/utils/common"
import LoadingCpn from "@/components/common/loadingcpn.vue"
import {useStore} from 'vuex'
import { useRouter } from 'vue-router'
import { getAlbum } from "@/api/api"
export default defineComponent({
    name:'artistsonglist',
    components:{
        LoadingCpn
    },
    props:[
        'isHot',
        'SongListhotSongs',
        'AlbumId'
    ],
    setup (props) {
        let state = reactive({
            songlistdata:[],
            hotClick:true,
            isHot:'',
            AlbumId:'',
        })
        onMounted(() => {
            if(props.AlbumId != state.AlbumId && undefined != props.AlbumId){
                state.AlbumId = props.AlbumId
                getAlbumDetail()
            }
        })
        watch(() => props.SongListhotSongs,(newVal:any) => {
            if(props.isHot != state.isHot && undefined != props.isHot){ //如果引入组件传的值不是通过接口获取或其他异步操作是不会执行updated生命周期
                state.songlistdata = newVal
            }
        },{immediate:true,deep:true})
        let itemRefs = ref()
        const setItemRef = (el:Element) => {
            if(el){
                itemRefs.value = el
            }
        }
        let router = useRouter()
        let checkAll = () => {
            if(props.isHot){
                itemRefs.value.style['max-height'] = 'none'
                state.hotClick = false
            }else{
                router.push({
                    path:'/album',
                    query:{
                        id:state.AlbumId
                    }
                })
            }
        }
        let getAlbumDetail = () => {
            getJson(getAlbum,{id:state.AlbumId},(res:any) => {
                if(['200', 'OK', 200].includes(res.code)){
                    state.songlistdata = res.songs
                }
            },(err:any) => {

            })
        }
        const store = useStore()
        let goAudioPlay = (data:any) => {
            let SongInfo = {
                id: data.id,
                name: data.name,
                picUrl: data.al.picUrl,
                song:{
                    artists: data.ar
                }
            }
            let arr:Array<object> = []
            let forArr = []
            forArr = JSON.parse(JSON.stringify(state.songlistdata))
            forArr.forEach((item:any) => {
                let obj = {
                    id: item.id,
                    name: item.name,
                    picUrl: item.al.picUrl,
                    song:{
                        artists: item.ar,
                        bMusic:{
                            playTime: item.dt,
                        }
                    }
                }
                arr.push(obj)
            })
            audioPlay(SongInfo,arr,store)
        }
        return {
            ...toRefs(state),
            playtime,
            checkAll,
            setItemRef,
            goAudioPlay
        }
    }
})
